body {
  height: 100vh;
  width: 100vw;
  margin: 0; padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.game {
  position: relative;
  border: solid 1em brown;
  width: 32em;
  height: 32em;
  font-size: min(2.75vh, 2.75vw);
}

.board {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: fit-content;
}
.row {
  display: flex;
  width: fit-content;
}
.square {
  height: 4em;
  width: 4em;
  position: relative;
}
.previous {
  top: 0; left: 0; right: 0; bottom: 0;
  position: absolute;
  /* background-color: #0004; */
  border: solid 0.7em #0002;
}
.selected {
  width: 4em;
  height: 4em;
  position: absolute;
  background-color: #ff07;
}
.black {
  background-color: #595;
}
.white {
  background-color: #eee;
}
.piece {
  position: absolute;
  color: blue;
  transition: top 0.5s, left 0.5s;
  height: 4em;
  width: 4em;
}
.piece.disabled {
  pointer-events: none;
}
.piece .check {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: solid 4px red;
}
.move, .movesquare {
  position: absolute;
  height: 4em;
  width: 4em;
}
.move {
  background-color: #ff07;
}
.movesquare::after {
  width: 1.5em;
  height: 1.5em;
  display: block;
  background-color: rgba(0,0,0,0.16);
  content: '';
  border-radius: 1em;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.move.kill {
  background-color: #f555
}